	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import duanshipin from '@/views/duanshipin/list'
	import address from '@/views/address/list'
	import shangpinxinxi from '@/views/shangpinxinxi/list'
	import fankuiyijian from '@/views/fankuiyijian/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import shangjia from '@/views/shangjia/list'
	import cart from '@/views/cart/list'
	import discussduanshipin from '@/views/discussduanshipin/list'
	import shangpinfenlei from '@/views/shangpinfenlei/list'
	import chat from '@/views/chat/list'
	import yonghu from '@/views/yonghu/list'
	import orders from '@/views/orders/list'
	import config from '@/views/config/list'
	import yijianfankui from '@/views/yijianfankui/list'
	import usersCenter from '@/views/users/center'
	import shangjiaRegister from '@/views/shangjia/register'
	import shangjiaCenter from '@/views/shangjia/center'

export const routes = [{
		path: '/login',
		name: 'login',
        meta: { title: '' },
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
        meta: { title: '' },
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: 'home',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true,
                title: ''
			}
		}, {
			path: '/updatepassword',
			name: 'updatepassword',
            meta: { title: '' },
			component: () => import('../views/updatepassword.vue')
		}

		,{
			path: '/usersCenter',
			name: 'usersCenter',
            meta: { title: '' },
			component: usersCenter
		}
		,{
			path: '/shangjiaCenter',
			name: 'shangjiaCenter',
            meta: { title: '' },
			component: shangjiaCenter
		}
		,{
			path: '/duanshipin',
			name: 'duanshipin',
            meta: { title: '' },
			component: duanshipin
		}
		,{
			path: '/address',
			name: 'address',
            meta: { title: '' },
			component: address
		}
		,{
			path: '/shangpinxinxi',
			name: 'shangpinxinxi',
            meta: { title: '' },
			component: shangpinxinxi
		}
		,{
			path: '/fankuiyijian',
			name: 'fankuiyijian',
            meta: { title: '' },
			component: fankuiyijian
		}
		,{
			path: '/storeup',
			name: 'storeup',
            meta: { title: '' },
			component: storeup
		}
		,{
			path: '/users',
			name: 'users',
            meta: { title: '' },
			component: users
		}
		,{
			path: '/shangjia',
			name: 'shangjia',
            meta: { title: '' },
			component: shangjia
		}
		,{
			path: '/cart',
			name: 'cart',
            meta: { title: '' },
			component: cart
		}
		,{
			path: '/discussduanshipin',
			name: 'discussduanshipin',
            meta: { title: '-' },
			component: discussduanshipin
		}
		,{
			path: '/shangpinfenlei',
			name: 'shangpinfenlei',
            meta: { title: '' },
			component: shangpinfenlei
		}
		,{
			path: '/chat',
			name: 'chat',
            meta: { title: '' },
			component: chat
		}
		,{
			path: '/yonghu',
			name: 'yonghu',
            meta: { title: '' },
			component: yonghu
		}
		,{
			path: '/orders',
			name: 'orders',
            meta: { title: '' },
			component: orders
		}
		,{
			path: '/config',
			name: 'config',
            meta: { title: '' },
			component: config
		}
		,{
			path: '/yijianfankui',
			name: 'yijianfankui',
            meta: { title: '' },
			component: yijianfankui
		}
		]
	},
	{
		path: '/shangjiaRegister',
		name: 'shangjiaRegister',
        meta: { title: '' },
		component: shangjiaRegister
	},
	{
		path: '/forget',
		name: 'forget',
        meta: { title: '' },
		component: () => import('../views/forget.vue')
	}
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
